@font-face {
  font-family: "Open Sans";
  src: url("../asset/OpenSans-Bold.woff2") format("woff2"), url("../asset/OpenSans-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "bold";
  src: url("../asset/OpenSans-Bold.woff2") format("woff2"), url("../asset/OpenSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "medium";
  src: url("../asset/OpenSans-Medium.woff2") format("woff2"), url("../asset/OpenSans-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "regular";
  src: url("../asset/OpenSans-Regular.woff2") format("woff2"), url("../asset/OpenSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "extrabold";
  src: url("../asset/OpenSans-ExtraBold.woff2") format("woff2"), url("../asset/OpenSans-ExtraBold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  border: none;
  list-style: none;
  color-scheme: light dark;
  line-height: 1.5em;
  color: var(--mainTextColor);
  font-family: "medium";
}

a, a:visited, a:active {
  text-decoration: none;
  outline: none;
}

p, li {
  margin-top: 0;
  color: var(--secondaryTextColor);
}

:root {
  --mainTextColor:#000;
  --mainBgColor:rgb(249,250,251);
  --mainBorderColor:rgb(218,218,218);
  --mainLinkColor:#0da2b8;
  --secondaryTextColor:rgb(51,51,51);
  --spanColor:rgb(21, 255, 0);
}

body {
  background-color: var(--mainBgColor);
}
body h1 {
  text-transform: uppercase;
}

span {
  color: var(--spanColor);
}