@use "./fonts" as *;
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border:none;
    list-style: none;
    color-scheme: light dark;
    line-height: 1.5em;
    color:var(--mainTextColor); 
    font-family: 'medium';  
}
a,a:visited,a:active{
    text-decoration: none;
    outline: none;
}
p,li{
    margin-top: 0;
    color: var(--secondaryTextColor);
}